/* ================= HERO SECTION ================= */
.loan-hero {
  position: relative;
  height: 450px;
  background-size: cover;
  background-position: bottom;
  display: flex;
  align-items: center;
}

/* Overlay */
.loan-overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.75);
}

/* Container */
.loan-container {
  position: relative;
  z-index: 2;
  max-width: 1200px;
  margin: auto;
  padding: 0 20px;
  width: 100%;
  display: grid;
  grid-template-columns: 1.2fr 0.8fr;
  gap: 40px;
  color: white;
}

/* ================= LEFT ================= */
.loan-left h1 {
  font-size: 36px;
  margin-bottom: 20px;
}

.loan-left ul {
  list-style: none;
  padding: 0;
  margin-bottom: 25px;
}

.loan-left li {
  margin-bottom: 8px;
  font-size: 16px;
}

/* ================= RIGHT FORM (TRANSPARENT) ================= */
.loan-right {
  background: transparent;
  color: white;
  padding: 25px;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
}

.loan-right h2 {
  font-size: 22px;
  margin-bottom: 10px;
}

.loan-right p {
  font-size: 14px;
  color: white;
  margin-bottom: 20px;
}

.loan-right input {
  width: 100%;
  padding: 12px;
  margin-bottom: 15px;
  border-radius: 6px;
  border: 1px solid rgba(255, 255, 255, 0.7);
  background: rgba(255, 255, 255, 0.1);
  color: white;
  font-size: 14px;
}

.loan-right input::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

/* ================= BUTTONS ================= */
.loan-btn {
  background: #e53935;
  color: white;
  border: none;
  padding: 12px 22px;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.3s ease;
}

.loan-btn:hover {
  background: #c62828;
}

.loan-full {
  width: 100%;
}

/* ================= RESPONSIVE ================= */
@media (max-width: 900px) {
  .loan-hero {
    height: auto;
    padding: 40px 0;
  }

  .loan-container {
    grid-template-columns: 1fr;
  }

  .loan-left h1 {
    font-size: 30px;
  }
}
